import { all, call, put, takeEvery } from 'redux-saga/effects';

import { notification } from 'antd';
import actions from './actions';
import { API_REPORT_SALES_BY_CUSTOMER, API_REPORT_SALES_BY_SHIFT, API_REPORT_SALES_CUSTOMER, API_REPORT_SALES_ITEM } from './apis';

export function* REPORT_SALES_CUSTOMER({ payload }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loadingReportCustomer: true
		}
	});

	try {
		const result = yield call(API_REPORT_SALES_CUSTOMER, payload);

		const { acknowledge, data } = result;
		if (acknowledge === 1) {
			yield put({
				type: actions.SET_STATE,
				payload: {
					reportSalesCustomer: data
				}
			});
		}
	} catch (e) {
		console.log(e);
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loadingReportCustomer: false
			}
		});
	}
}

