export function* REPORT_SALES_BY_SHIFT({ payload, history }) {
	yield put({
		type: actions.SET_STATE,
		payload: {
			loadingReportShift: true
		}
	});

	try {
		const result = yield call(API_REPORT_SALES_BY_SHIFT, payload);
		const { acknowledge = 0, message = '', data = {}, isLog = true } = result;

		if (acknowledge === 1) {
			yield put({
				type: actions.SET_STATE,
				payload: {
					reportSalesShift: data
				}
			});

			return;
		}

		if (!isLog) {
			yield put({
				type: 'auth/LOGOUT_POS',
				history
			});
		}

		notification.error({
			message: 'Gagal!',
			description: message
		});
	} catch (error) {
		console.log(error);
	} finally {
		yield put({
			type: actions.SET_STATE,
			payload: {
				loadingReportShift: false
			}
		});
	}
}

export default function* rootSaga() {
	yield all([
		takeEvery(actions.REPORT_SALES_CUSTOMER, REPORT_SALES_CUSTOMER),
		takeEvery(actions.REPORT_SALES_BY_CUSTOMER, REPORT_SALES_BY_CUSTOMER),
		takeEvery(actions.REPORT_SALES_ITEM, REPORT_SALES_ITEM),
		takeEvery(actions.REPORT_SALES_BY_SHIFT, REPORT_SALES_BY_SHIFT)
	]);
}
